/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background-color: #d9d9d9;
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  background-image: linear-gradient(rgba(0, 0, 0, 0.65) 0%, rgba(0, 0, 0, 0.65) 100%), url("../img/bg-main.webp");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  color: #ffffff;
}

p {
  font-size: 2.3rem;
}

[class*=container],
.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

[class*=__heading] {
  color: #ffa43c;
}

img {
  max-width: 100%;
  display: block;
  pointer-events: none;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.section {
  padding: 3rem 0;
}

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3.2rem;
}

[class$=heading] {
  margin-bottom: 2rem;
}

a {
  text-decoration: none;
}

/** Media Queries **/
.content {
  padding-top: 2rem;
  display: grid;
  grid-template-columns: 4fr 1fr;
}

.tabs {
  display: flex;
  padding-bottom: 3rem;
}

.tabs__column {
  display: flex;
  flex-direction: column;
}

.tabs__cell {
  text-align: center;
  min-width: 2rem;
  padding: 0.5rem;
  font-weight: 700;
  text-transform: lowercase;
}

.tabs__cell-active {
  position: relative;
}

.tabs__cell-active::before {
  content: "";
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  background-color: #99582a;
  top: 0;
  left: 0;
  animation: blinker 1s linear infinite;
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}
.shortcuts__grid {
  display: flex;
  flex-direction: column;
}

.shortcut {
  display: flex;
  column-gap: 1.5rem;
}

.shortcut__key {
  text-align: center;
  font-weight: 700;
}

.print {
  margin-top: 2.5rem;
  margin-inline: auto;
  padding: 1.2rem;
  display: block;
  text-transform: capitalize;
  border-radius: 2rem;
  border: none;
  background-color: #99582a;
  width: 80%;
  color: #ffffff;
  cursor: pointer;
}

.print:hover {
  background-color: #5d351a;
  transition: 300ms ease-in-out;
}

.description {
  display: flex;
  justify-content: center;
  column-gap: 5rem;
}

.description__part {
  padding-block: 0.5rem;
  font-size: 1.8rem;
}

.description__part label {
  color: #ffa43c;
  font-weight: 700;
  display: block;
  padding-bottom: 0.5rem;
}

.description__part input {
  text-indent: 0.5rem;
  padding-block: 0.2rem;
  vertical-align: center;
}